/* Fonts */
@import url("https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600;700&display=swap");
h1,
h2,
h3,
h4,
h5 {
  color: #000000;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* ------- Reset ------- */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Mulish", sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
  color: #7d7987;
}

/* ------- Disable aos animation delay on mobile devices ------- */
@media screen and (max-width: 768px) {
  [data-aos-delay] {
    transition-delay: 0 !important;
  }
}
.section-separator {
  padding: 60px 0;
  overflow: hidden;
}

.tra-title {
  padding-bottom: 80px;
  text-align: center;
}
.tra-title h2 {
  margin: 0;
  padding: 0;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.5;
}
.tra-title span {
  width: 56px;
  height: 2px;
  background: #000000;
  margin: 26px auto 32px;
  display: block;
}
@media (min-width: 992px) {
  .tra-title p {
    width: 80%;
    margin: auto;
  }
}

.btn-brand-outline, .btn-brand-fill, .btn-brand {
  padding: 16px 40px;
  line-height: 1;
  border-radius: 50px;
  border: none;
  outline: none;
  box-shadow: none;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 1rem;
  font-weight: 700;
}

.btn-brand-fill {
  background: #458ff6;
  color: #ffffff;
}
.btn-brand-fill:hover {
  color: #fff;
  background: #5d9ef7;
}

.btn-brand-outline {
  background: #ffffff;
  color: #458ff6;
  border: 1px solid #458ff6;
}
.btn-brand-outline:hover {
  background-color: #458ff6;
  color: #ffffff;
}

/* ------- Header ------- */
.header {
  background-color: transparent;
  top: 56px;
  width: 100%;
  transition: all 0.3s;
}
.header-scrolled {
  background-color: #ffffff;
  top: 0;
  padding: 8px 0;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
}

/* ------- Navigation ------- */
/* Desktop Nav  */
.nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-menu .nav-menu-hamburger {
  position: relative;
  width: 28px;
  height: 28px;
  z-index: 9999;
  cursor: pointer;
}
.nav-menu .nav-menu-hamburger span::after, .nav-menu .nav-menu-hamburger span::before, .nav-menu .nav-menu-hamburger span {
  width: 28px;
  height: 3px;
  display: inline-block;
  background: #1b1b22;
  position: absolute;
}
.nav-menu .nav-menu-hamburger span {
  top: 12.5px;
  transition: all 0.3s;
}
.nav-menu .nav-menu-hamburger span::before {
  content: "";
  top: -8px;
  transition: all 0.3s;
}
.nav-menu .nav-menu-hamburger span::after {
  content: "";
  top: 8px;
  transition: all 0.3s;
}
.nav-menu .nav-menu-hamburger.open span {
  left: -2px;
  transform: rotate(225deg);
}
.nav-menu .nav-menu-hamburger.open span::before {
  top: 0;
  left: 0;
  transform: rotate(90deg);
}
.nav-menu .nav-menu-hamburger.open span::after {
  opacity: 0;
}
.nav-menu ul {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
}
.nav-menu ul li {
  margin-right: 40px;
}
.nav-menu ul li:last-child {
  margin-right: 0;
}
.nav-menu ul li a {
  font-size: 18px;
  line-height: 23px;
  display: block;
  text-decoration: none;
  color: #7d7987;
  transition: all 0.3s;
}
.nav-menu ul li a:hover, .nav-menu ul li a.active {
  font-weight: 500;
  color: #1f1534;
}

/* Mobile Nav */
@media (max-width: 991px) {
  .mobile-nav-active {
    overflow: hidden;
  }
  .mobile-nav-active .logo {
    pointer-events: none;
  }

  .nav-menu ul {
    position: fixed;
    top: 55px;
    left: 15px;
    right: 15px;
    bottom: 15px;
    background: #ffffff;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px;
    z-index: 9998;
    align-items: flex-start;
    box-shadow: 0 0 0 500px rgba(69, 143, 246, 0.65);
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  .nav-menu ul.open {
    opacity: 1;
    pointer-events: auto;
  }
  .nav-menu ul li {
    margin: 0;
  }
  .nav-menu ul a {
    display: block;
    padding: 10px 20px;
    font-weight: 500;
  }
}
/* ------- Footer ------- */
.footer {
  background: linear-gradient(183.41deg, #67c3f3 -8.57%, #5a98f2 82.96%);
  color: #ffffff;
}
.footer a {
  color: #ffffff;
  text-decoration: none;
}
.footer-content {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  grid-template-rows: auto;
  gap: 16px;
}
@media (max-width: 991px) {
  .footer-content {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767px) {
  .footer-content {
    grid-template-columns: 1fr;
  }
}
.footer-info {
  margin-bottom: 30px;
}
.footer-info p {
  margin: 18px 0 30px;
}
.footer-info .copyright {
  margin: 0;
}
.footer-links {
  margin-bottom: 30px;
}
.footer-links h4 {
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 12px;
  color: #ffffff;
  position: relative;
}
.footer-links ul li {
  padding: 10px 0;
  line-height: 1;
}
.footer-links ul li:first-child {
  padding-top: 0;
}
.footer-links ul a {
  line-height: 1;
  transition: all 0.3s;
}
.footer-links ul a:hover {
  font-weight: 400;
}

/* ------- Hero ------- */
.hero {
  padding-top: 170px;
}
.hero .container {
  display: flex;
  flex-wrap: nowrap;
}
@media (max-width: 991px) {
  .hero .container {
    flex-wrap: wrap;
  }
}
.hero-text {
  margin-top: 100px;
  flex: 1;
}
@media (max-width: 991px) {
  .hero-text {
    margin-top: 0;
    margin-bottom: 30px;
  }
}
.hero-text h1 {
  font-size: 48px;
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 25px;
}
@media (max-width: 991px) {
  .hero-text h1 {
    font-size: 40px;
  }
}
.hero-text p {
  font-weight: 300;
  font-size: 20px;
  line-height: 1.6;
  margin-bottom: 45px;
}
.hero-image {
  text-align: right;
}
@media (max-width: 991px) {
  .hero-image {
    text-align: center;
  }
}
.hero-image .image img {
  max-width: 80%;
}
@media (max-width: 991px) {
  .hero-image .image img {
    max-width: 100%;
  }
}

/* ------- Services ------- */
.services {
  background: url("../images/bg-2.png") no-repeat center left/50%;
}
.services .service {
  padding: 62px 52px 47px 36px;
  box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
  border-radius: 20px;
  margin-bottom: 37px;
  background: #ffffff;
  color: #7d7987;
  text-decoration: none;
}
@media (max-width: 991px) {
  .services .service {
    text-align: center;
  }
}
.services .service:hover .icon img {
  transform: scale(1.1);
}
.services .service:hover h4 {
  color: #458ff6;
}
.services .service .icon {
  margin-bottom: 25px;
  width: 60px;
  height: 60px;
}
@media (max-width: 991px) {
  .services .service .icon {
    margin-left: auto;
    margin-right: auto;
  }
}
.services .service .icon img {
  width: auto;
  height: 100%;
  transition: all 0.3s;
}
.services .service h4 {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 16px;
  transition: all 0.3s;
}
.services .service p {
  font-size: 16px;
  line-height: 1.6;
  margin: 0;
}

/* ------- Providers ------- */
.providers .provider + .provider {
  margin-top: 100px;
}
.providers .provider-text {
  padding-top: 57px;
}
.providers .provider-text .tra-title {
  padding-bottom: 0;
  text-align: left;
}
.providers .provider-text .tra-title span {
  margin-left: 0;
}
.providers .provider-text p {
  margin-bottom: 37px;
}
.providers .provider-text .btn-brand-outline {
  margin-bottom: 32px;
}
.providers .provider-image img {
  max-width: 90%;
  height: auto;
}

/* ------- Testimonials ------- */
.testimonials .testimonial {
  color: #ffffff;
  background: linear-gradient(208.18deg, #67c3f3 9.05%, #5a98f2 76.74%);
  font-weight: 400;
  border-radius: 24px;
}
.testimonials .testimonial-inner {
  display: flex;
  padding: 64px 118px 90px;
}
@media (max-width: 991px) {
  .testimonials .testimonial-inner {
    flex-direction: column;
    padding: 30px;
    text-align: center;
  }
}
.testimonials .testimonial-image {
  margin-right: 30px;
  --tsm-img-size: 133px;
  width: var(--tsm-img-size);
  height: var(--tsm-img-size);
  border-radius: 50%;
  overflow: hidden;
  object-fit: cover;
  align-self: center;
}
@media (max-width: 991px) {
  .testimonials .testimonial-image {
    margin-bottom: 20px;
  }
}
.testimonials .testimonial-image img {
  width: 100%;
  height: auto;
}
.testimonials .testimonial-text {
  flex: 1;
}
.testimonials .testimonial-text .tra-title {
  padding-bottom: 0;
  text-align: left;
}
@media (max-width: 991px) {
  .testimonials .testimonial-text .tra-title {
    font-size: 32px;
    text-align: center;
  }
}
.testimonials .testimonial-text .tra-title h2 {
  color: #ffffff;
}
@media (max-width: 991px) {
  .testimonials .testimonial-text .tra-title h2 {
    line-height: 1.2;
    font-size: 30px;
  }
}
.testimonials .testimonial-text .tra-title span {
  background: #ffffff;
  margin-left: 0;
}
@media (max-width: 991px) {
  .testimonials .testimonial-text .tra-title span {
    margin-left: auto;
  }
}
.testimonials .testimonial-name {
  padding-right: 110px;
  align-self: center;
}
.testimonials .testimonial-name h4 {
  color: #ffffff;
  font-size: 22px;
  font-weight: 700;
}
.testimonials .testimonial-name span {
  font-size: 18px;
}
@media (max-width: 991px) {
  .testimonials .testimonial-name {
    padding-right: 0;
    margin-bottom: 20px;
    align-self: auto;
  }
}
.testimonials .testimonial-desc {
  margin-bottom: 0;
  flex: 1;
}
.testimonials .carousel-indecator-container {
  position: relative;
  width: 100%;
  height: 30px;
  margin-top: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.testimonials .carousel-indicators {
  margin: 0;
  display: flex;
  padding: 0 90px;
  position: static;
}
@media (max-width: 991px) {
  .testimonials .carousel-indicators {
    padding-left: 60px;
    padding-right: 60px;
  }
}
.testimonials .carousel-indicators button {
  border: none;
  outline: none;
  width: 10px !important;
  height: 10px !important;
  border-radius: 50%;
  background-color: #458ff6;
}
.testimonials .carousel-indicators button + button {
  margin-left: 18px !important;
}
.testimonials .carousel-prev, .testimonials .carousel-next {
  border: none;
  background: transparent;
  color: #458ff6;
  opacity: 0.6;
  transition: all 0.3s;
}
.testimonials .carousel-prev:hover, .testimonials .carousel-next:hover {
  opacity: 1;
}
.testimonials .carousel-control-prev-icon, .testimonials .carousel-control-next-icon {
  position: relative;
  background: none;
}

/* ------- Articles ------- */
.articles {
  background: url("../images/bg-1.png") no-repeat center right/40%;
}
.articles .article-box {
  background: #ffffff;
  box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
  border-radius: 20px;
  overflow: hidden;
}
.articles .article-box .image img {
  width: 100%;
  height: auto;
}
.articles .article-box .text {
  padding: 24px 30px 35px;
}
.articles .article-box .text h4 {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 12px;
  line-height: 1.5;
}
.articles .article-box .text p {
  font-size: 16px;
  line-height: 1.75;
  margin-bottom: 25px;
}
.articles .article-box .text a {
  font-size: 16px;
  line-height: 1.75;
  text-decoration: none;
  color: #4089ed;
  transition: all 0.3s;
}
.articles .article-box .text a:hover {
  color: #5d9ef7;
}
.articles .article-box .text a:hover > svg {
  transform: translateX(4px) scale(1.2);
}
.articles .article-box .text a > svg {
  margin-left: 8px;
  transition: all 0.3s;
}